import React from 'react'
import Actividad from './Actividad'

const Lista = ({ tareas, idActiv,  borrarTodo }) => {
    return (
        <div>
            {tareas.map((tarea)=>  (
                <Actividad 
                key={tarea.id} 
                title={tarea.title} 
                idActiv={idActiv} 
                id={tarea.id} 
                isCompleted={tarea.isCompleted}
                borrarTodo={borrarTodo}
                />   
            ))}
           
            
        </div>
    )
}

export default Lista
