import React, { useState } from 'react';
import Form from './components/Form'
import Lista from './components/Lista'
import { v4 } from 'uuid'
import Nav from './components/Nav'
import Footer from './components/Footer'
import './App.css';

function App() {
  const [tareas, setTareas] = useState ([])

  const idActiv = (id) =>{
    setTareas(tareas.map(tarea => {
      if (tarea.id === id)
        tarea.isCompleted = !tarea.isCompleted
      return tarea;
    }))
  }

  const borrarTodo = (id) =>{
      setTareas(tareas.filter((tarea) => tarea.id !== id))
  }

  const addActiv = (texto) =>{
    const nuevaActiv = {
      id: v4(),
      title: texto,
      isCompleted: false
    }
    setTareas([...tareas, nuevaActiv])
  }

  return (
      <div>
        <Nav/>
        <Form addActiv={addActiv}/>
        <Lista tareas={tareas} idActiv={idActiv} borrarTodo={borrarTodo} />
        <Footer/>
      </div>
  );
}

export default App;
