import React, { useState } from 'react'
import { FormControl, Typography, Container, TextField, Button } from '@mui/material';

const Form = ({addActiv}) => {
    const [texto, setTexto] = useState ("");

    const handleSubmit = (e) =>{
        e.preventDefault();
        addActiv(texto)
        setTexto("")
    }

    return (
        <Container maxWidth='sm'>
            <form onSubmit={handleSubmit}>
                <FormControl fullWidth={true}>
                    <TextField 
                    label='Ingrese una tarea'  
                    required={true} 
                    style={{marginTop: 20}} 
                    value={texto}
                    onChange={(e) =>setTexto(e.target.value)}
                    />
                    <Button 
                    variant='contained' 
                    color='primary'
                    style={{marginTop: 10, marginBottom: 10}} 
                    type='submit'>
                         Añadir 
                    </Button>
                </FormControl>
                
            </form>
            <Typography variant='body1' style={{color:"green", textAlign:"center"}}>
                Color Verde: Actividad realizada     
            </Typography>
            <Typography  variant='body1' style={{color:"red", textAlign:"center"}}>
                Color Rojo: Actividad sin realizar
            </Typography>
            <Typography variant='subtitle2' style={{color:"grey", textAlign:"center"}}>
            Hacer click en el icono ✓ para cambiar a realizado o no realizado 
            </Typography>
        </Container>
    )
}

export default Form
