import React from 'react';
import { Card, CardContent, Typography, Container, IconButton } from '@mui/material';
import { Check, Delete } from "@mui/icons-material";

const Actividad = ({title, idActiv, id, isCompleted, borrarTodo}) => {
    
    const activCompletada = () => idActiv(id);
   
    const estiloTarea = isCompleted ? {color:"green"} : {color:"red"}

    const borrarTarea = () => borrarTodo(id)
    
    return (
        <div>
            <Container>
                <Card 
                variant='outlined' 
                style={{ background: 'lightgray', marginTop: 15}}>
                    <CardContent>

                        <Typography variant='h5' component='h2' style={estiloTarea}>
                            <IconButton onClick={activCompletada} >
                                <Check style={estiloTarea}/>
                            </IconButton>
                            {title}
                            <IconButton style={{float: "right"}} onClick={borrarTarea}>
                                <Delete style={{color: 'black'}}/>
                            </IconButton>
                        </Typography>
                    </CardContent>
                </Card>
            </Container>
        </div>
    )
}

export default Actividad
